body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
.section {
    width: 100%;
    height: 100vh;
    position: relative;
    overflow: hidden;
}
.section_color_green {
    background: rgba(0, 128, 0, 0.6);
    transition: all 2s ease;
}
.section_color_blue {
    background: #0000ffA0;
    transition: all 2s ease;
}
.section_color_yellow {
    background: #a52a2aa0;
    transition: all 2s ease;
}
.section_color_magenta {
    background: #ff00ffA0;
    transition: all 2s ease;
}
.section_color_black {
    background: #000000A0;
    transition: all 2s ease;
}
.btn {
    position: absolute;
    bottom: 20px;
    right: 0;
    left: 0;
    margin: auto;
    padding: 0;
    border-radius: 50%;
    border: none;
    width: 80px;
    height: 80px;
    box-shadow: 2px 2px 10px rgba(0, 0, 0, .2);
    cursor: pointer;
    font-size: 1.2rem;
    font-weight: 200;
    color: rgba(255, 255, 255, .8);
    text-transform: uppercase;
    text-align: center;
    line-height: 80px;
    background: linear-gradient(to bottom right, rgba(0, 128, 0, 0.1) 0%, rgba(0, 128, 0, 0.3) 1000%);
}
.btn:hover {
    box-shadow: 2px 2px 20px rgba(0, 0, 0, .4);
    transition: all .7s ease;
}
.btn:active {
    -webkit-transform: scale(.9);
            transform: scale(.9);
}
.btn, .btn:hover {
    text-decoration: none;
}
.title {
    margin-top: 0;
    font-size: 100px;
    font-weight: 900;
    font-family: 'Montserrat', Arial, Helvetica, sans-serif;
    text-align: center;
    color: white;
}
.text {
    padding: 20px 40px;
    font-size: 30px;
    color: #fff;
    font-weight: 300;
}
.btn svg {
    width: 80px;
    height: 80px;
}
@media (max-width: 768px) {
    .text {
        font-size: 1rem;
    }
    .title {
        font-size: 2rem;
    }
    .section {
        padding-top: 40px;
        padding-bottom: 0;
        height: 100vh;
    }
}
